<template>
  <div class="route-result-container">
    <router-link :to="{ name: 'placeDetails', params: { id: route.id }}">
      <div class="route-result-item">
        <div class="route-result-item__image">
        </div>
        <div class="route-result-item__description">
          <div class="route-result-item__heading">
            <div class="route-result-item__duration">
              <div class="duration-total">Продолжительность: {{route.duration}} часов, {{route.distance}} км</div>
              <div class="duration-details">Время в дороге: {{route.roadTime}} часов.  Время в точках: {{route.pointTime}} часа. Количество точек: {{route.points.length -1}}.</div>
            </div>
          </div>
          <div class="route-result-item__details">
              <span class="route-point" v-for="(point, index) in route.points">
                <span class="route-point__name">{{ point.startPointName }}</span> -
                <span class="route-point__duration">{{ point.duration }} часа, </span>
                <span class="route-point__distance">{{ point.distance }} км - </span>
                <span class="route-point__name">{{ point.endPointName }}</span><span
                v-if="index !== route.points.length - 1">,</span>
              </span>
            <div class="route-result-item__tags">
              <span class="item-tag" v-for="tag in route.tags">
                {{ tag.title }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
  export default {
    props: ['route'],
    methods: {
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../styles/colors";

  .route-result-item {
    display: flex;
    margin-bottom: 10px;
    max-height: 150px;
    overflow: hidden;

    .route-result-item__image {
      min-width: 200px;
      height: 140px;
      background: url("https://espanarusa.com/files/autoupload/77/21/51/ltcojfsx345156.jpg") no-repeat;
      background-size: contain;
    }

    .route-result-item__description {
      padding-left: 10px;
    }

    .route-result-item__heading .route-result-item__duration {
      color: $c-primary;

      .duration-total {
        font-weight: 600;
        line-height: 14px;
      }
    }
    .route-result-item__details {
      color: $c-black;
    }

    .route-point__name {
      font-weight: 600;
    }
  }

  .item-tag {
    background: $c-white;
    border: 1px solid $c-primary;
    border-radius: 6px;
    padding: 0 4px;
    margin-right: 5px;
  }

  a {
    text-decoration: none;
  }
</style>



