<template>
  <div class="place-page">
    <breadcrumbs :data="placeBreadcrumbs"></breadcrumbs>
    <div class="row">
      <div class="col-12 col-md-9">
        <carousel :slides="placeAlbum" class="mg-b-10"></carousel>
      </div>
      <div class="col-12 col-md-3">
        <stats></stats>
      </div>
    </div>
    <div class="row">
      <div class="col-12 col-md-3">
        <opportunities :data="placeOpportunities"></opportunities>
      </div>
      <div class="col-12 col-md-6">
        <div class="place-info">
          <div class="info-block">
            <div class="place-categories">
              <span class="category">#Природа</span>
              <span class="category">#Национальные парки</span>
            </div>
            <h5>{{placeDescription.name}}</h5>
            <div v-html="placeDescription.description"></div>
          </div>
          <div class="info-block">
            <what-to-take :data="placeWhatToTake"></what-to-take>
          </div>
          <div class="info-block">
            <contacts :data="placeContacts"></contacts>
          </div>
          <div class="info-block">
            <working-hours :data="placeWorkingHours"></working-hours>
          </div>
          <div class="info-block">
            <how-to-get-there :data="placeHowToGetThere"></how-to-get-there>
          </div>
          <div class="info-block">
            <other-info :data="placeOtherInfo"></other-info>
          </div>
        </div>
      </div>
      <div class="col-12 col-md-3">
        <vak-map :data="placeDescription.point" class="map-desktop"></vak-map>
        <articles></articles>
        <weather></weather>
      </div>
    </div>
    <div class="row">
      <linked-places :data="linkedPlaces"></linked-places>
      <ads-wide></ads-wide>
      <best-routes></best-routes>
    </div>
  </div>

</template>

<script>
  import { mapGetters } from 'Vuex';
  import Carousel from '@/components/Carousel';
  import VakOpportunities from '@/components/VakOpportunities';
  import VakWhatToTake from '@/components/VakWhatToTake';
  import VakContacts from '@/components/VakContacts';
  import VakWorkingHours from '@/components/VakWorkingHours';
  import VakHowToGetThere from '@/components/VakHowToGetThere';
  import VakOtherInfo from '@/components/VakOtherInfo';
  import VakMap from '@/components/VakMap';
  import VakBreadcrumbs from '@/components/VakBreadcrumbs';
  import VakStats from '@/components/VakStats';
  import VakArticles from '@/components/VakArticles';
  import VakLinkedPlaces from '@/components/VakLinkedPlaces';
  import VakWeather from '@/components/VakWeather';
  import VakAdsWide from '@/components/VakAdsWide';
  import VakBestRoutes from '@/components/VakBestRoutes';

  export default {
    name: 'home',
    created() {
      this.$store.dispatch('getBreadcrumbs', { placeId: this.$route.params.id });
      this.$store.dispatch('getPlaceAlbum', { placeId: this.$route.params.id });
      this.$store.dispatch('getDescription', { placeId: this.$route.params.id });
      this.$store.dispatch('getContacts', { placeId: this.$route.params.id });
      this.$store.dispatch('getHowToGetThere', { placeId: this.$route.params.id });
      this.$store.dispatch('getOpportunities', { placeId: this.$route.params.id });
      this.$store.dispatch('getOtherInfo', { placeId: this.$route.params.id });
      this.$store.dispatch('getWhatToTake', { placeId: this.$route.params.id });
      this.$store.dispatch('getWorkingHours', { placeId: this.$route.params.id });
      this.$store.dispatch('getLinkedPlaces', { placeId: this.$route.params.id });
    },
    computed: {
      ...mapGetters([
        'placeAlbum',
        'placeBreadcrumbs',
        'placeDescription',
        'placeContacts',
        'placeHowToGetThere',
        'placeOpportunities',
        'placeOtherInfo',
        'placeWhatToTake',
        'placeWorkingHours',
        'linkedPlaces'
      ])
    },
    components: {
      carousel: Carousel,
      opportunities: VakOpportunities,
      whatToTake: VakWhatToTake,
      contacts: VakContacts,
      workingHours: VakWorkingHours,
      howToGetThere: VakHowToGetThere,
      otherInfo: VakOtherInfo,
      vakMap: VakMap,
      breadcrumbs: VakBreadcrumbs,
      stats: VakStats,
      weather: VakWeather,
      articles: VakArticles,
      linkedPlaces: VakLinkedPlaces,
      adsWide: VakAdsWide,
      bestRoutes: VakBestRoutes
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import "../styles/variables.scss";
  @import "../styles/colors.scss";

  .place-page {
    .place-info {
      font-size: 1rem;

      .info-block {

        .info-title {
          display: inline-block;
          vertical-align: middle;
          margin: 0;
        }
      }
    }

    .mg-b-10 {
      margin-bottom: 10px;
    }

    .map-desktop {
      @media (max-width: map-get($grid-breakpoints, md)) {
        display: none;
      }
    }

    .map-mobile {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 400px;
      margin-bottom: 60px;

      @media (min-width: map-get($grid-breakpoints, md)) {
        display: none;
      }
    }

    .place-main-section {
      @media (max-width: map-get($grid-breakpoints, md)) {
        padding-bottom: 400px;
      }
    }

    .place-categories {
      margin-bottom: 20px;
      .category {
        padding: 5px 10px;
        background-color: $c-primary;
        color: $c-white;
        border-radius: 0.3rem;
      }
    }
  }
</style>



