      <place-preview v-for="place in places" :place="place" :key="place.id"></place-preview>
    </div>
  </div>
</template>

<script>
  import VakSearchBox from '@/components/VakSearchBox';
  import VakPlacePreview from '@/components/VakPlacePreview';

  export default {
    name: 'home',
    data() {
      return {
        isLoading: false,
        places: [
          { id: 1, title: 'Первое место'},
          { id: 2, title: 'Второе место'},
          { id: 3, title: 'Третье место'},
          { id: 4, title: 'Четвертое место'},
          { id: 5, title: 'Пятое место'},
          { id: 6, title: 'Шестое место'},
          { id: 7, title: 'Седьмое место'},
          { id: 8, title: 'Восьмое место'}
        ]
      };
    },
    created() {
    },
    computed: {

    },
    components: {
      searchBox: VakSearchBox,
      placePreview: VakPlacePreview
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .page-header .page-title {
    margin-bottom: 0;
    padding-top: 10px;
  }
</style>



