import * as types from './mutationTypes';
import { getAlbum, getBreadcrumbs, getDescription, getContacts, getHowToGetThere, getOpportunities, getOtherInfo, getWhatToTake, getWorkingHours, getLinkedPlaces } from '../../../api/placeDetails';

export const actions = {
  getPlaceAlbum({ commit }, { placeId }) {
    return getAlbum(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_ALBUM, response);
    });
  },
  getBreadcrumbs({ commit }, { placeId }) {
    return getBreadcrumbs(placeId).then((response) => {
      commit(types.RECEIVE_BREADCRUMBS, response);
    });
  },
  getDescription({ commit }, { placeId }) {
    return getDescription(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_DESCRIPTION, response);
    });
  },
  getContacts({ commit }, { placeId }) {
    return getContacts(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_CONTACTS, response);
    });
  },
  getHowToGetThere({ commit }, { placeId }) {
    return getHowToGetThere(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_HOW_TO_GET_THERE, response);
    });
  },
  getOpportunities({ commit }, { placeId }) {
    return getOpportunities(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_OPPORTUNITIES, response);
    });
  },
  getOtherInfo({ commit }, { placeId }) {
    return getOtherInfo(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_OTHER_INFO, response);
    });
  },
  getWhatToTake({ commit }, { placeId }) {
    return getWhatToTake(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_WHAT_TO_TAKE, response);
    });
  },
  getWorkingHours({ commit }, { placeId }) {
    return getWorkingHours(placeId).then((response) => {
      commit(types.RECEIVE_PLACE_WORKING_HOURS, response);
    });
  },
  getLinkedPlaces({ commit }, { placeId }) {
    return getLinkedPlaces(placeId).then((response) => {
      commit(types.RECEIVE_LINKED_PLACES, response);
    });
  }
};



