<template>
  <div class="categories-list">
    <h5>Места (маршрутные точки)</h5>
    <div class="category-item" v-for="category in categories">
      <div class="category-item__heading">{{ category.name }}</div>
      <ul>
        <li v-for="subcategory in category.subcategories">{{ subcategory.name }}</li>
      </ul>
    </div>
  </div>
</template>

<script>

  export default {
    props: ['categories']
  };

</script>

<style lang="scss">
  @import "../styles/colors.scss";

  .categories-list {
    background-color: $c-white;
    border-radius: 6px;
    padding: 10px;
    margin-top: 10px;

    .category-item ul {
      list-style: none;
      padding-left: 10px;
    }

    .category-item__heading {
      font-weight: 600;
    }
  }

</style>



