<template>
  <div class="route-page">
    <div class="row">
      <div class="col-12 col-md-8">
        <div class="row place-main-section">
          <div class="col-12 col-md-4">
            <opportunities :data="placeOpportunities"></opportunities>
          </div>
          <div class="col-12 col-md-8">
            <!--<vak-map :data="routeDescription.points" class="map-desktop"></vak-map>-->
            <div class="place-info">
              <div class="info-block">
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-12 col-md-4">
      </div>
    </div>
  </div>

</template>

<script>
  import { mapGetters } from 'Vuex';
  import VakOpportunities from '@/components/VakOpportunities';
  import VakMap from '@/components/VakMap';

  export default {
    name: 'routeDetails',
    created() {
      this.$store.dispatch('getOpportunities', { placeId: 1 });
    },
    data() {
      return {
        routeDescription: {
          id: 1,
          duration: 180,
          distance: 200,
          roadTime: 100,
          pointTime: 80,
          points: [
            {
              startPointName: 'Минск',
              duration: 120,
              distance: 120,
              endPointName: 'Замок Гедимина'
            },
            {
              startPointName: 'Минск',
              duration: 120,
              distance: 120,
              endPointName: 'Замок Гедимина'
            },
            {
              startPointName: 'Минск',
              duration: 120,
              distance: 120,
              endPointName: 'Замок Гедимина'
            },
            {
              startPointName: 'Минск',
              duration: 120,
              distance: 120,
              endPointName: 'Замок Гедимина'
            },
            {
              startPointName: 'Минск',
              duration: 120,
              distance: 120,
              endPointName: 'Замок Гедимина'
            }
          ],
          tags: [ { title: 'Каталог уровень 1' }, { title: 'Каталог уровень 2' }, { title: 'Крепости' } ]
        }
      };
    },
    computed: {
      ...mapGetters([
        'placeOpportunities'
      ])
    },
    components: {
      opportunities: VakOpportunities,
      vakMap: VakMap
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import "../styles/variables.scss";

  .route-details-page {
    .place-info {
      font-size: 1rem;

      .info-block {
        .info-title {
          display: inline-block;
          vertical-align: middle;
          margin: 0 0 0 10px;
        }
      }
    }

    .mg-b-10 {
      margin-bottom: 10px;
    }

    .map-desktop {
      @media (max-width: map-get($grid-breakpoints, md)) {
        display: none;
      }
    }

    .map-mobile {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 400px;
      margin-bottom: 60px;

      @media (min-width: map-get($grid-breakpoints, md)) {
        display: none;
      }
    }

    .place-main-section {
      @media (max-width: map-get($grid-breakpoints, md)) {
        padding-bottom: 400px;
      }
    }
  }
</style>



