<template>
  <nav class="main-navbar sticky-top">
    <div class="main-navbar__container container-fluid">
      <a href="#/home" class="main-navbar__logo home-link">
        <div class="main-navbar__logo-title">Вакол</div>
      </a>
      <div class="main-navbar__menu">
        <router-link class="main-navbar__menu-item" to="/home" tag="li">Главная</router-link>
        <router-link class="main-navbar__menu-item" to="/places" tag="li">Места</router-link>
        <router-link class="main-navbar__menu-item" to="/routes" tag="li">Маршруты</router-link>
        <router-link class="main-navbar__menu-item" to="/about" tag="li">О нас</router-link>
      </div>
      <div class="main-navbar__tools">
        <div class="form-group">
          <input class="form-control" placeholder="Введите здесь место, которое нужно найти">
          <i class="fa fa-search"></i>
        </div>
        <span class="btn btn-secondary btn-lang">RU</span>
      </div>
    </div>
  </nav>
</template>

<script>

  export default {
    name: 'navbar'
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../styles/variables";
  @import "../styles/colors";

  $logo_width: 102px;
  $menu_width: 390px;
  $tools_max-width: 450px;

  .main-navbar__container {
    display: flex;
  }

  .main-navbar {
    height: 65px;
    padding: 10px 0;
    background-color: $c-muted;
    color: $c-dark;
  }

  ,main-navbar__logo, main-navbar__menu, main-navbar__tools {
    flex: 1;
   }

  .main-navbar__logo {
    padding: 10px;
    color: $c-white;
    text-decoration: none;
    border-radius: 6px;
    background-color: $c-dark;
    width: $logo_width;
  }

  .main-navbar__logo-title {
    line-height: 1;
    font-size: 1.6rem;
    text-transform: uppercase;
  }

  .main-navbar__menu {
    align-self: center;
    padding-left: 20px;
    width: $menu_width;
  }

  .main-navbar__tools {
    align-self: center;
    width: calc(100% - #{$menu_width} - #{$logo_width});
    margin-left: auto;
    max-width: $tools_max-width;
    display: flex;

    .form-group {
      position: relative;
      margin-bottom: 0;
      display: inline-block;
      flex: 1;

      .fa-search {
        position: absolute;
        right: 10px;
        top: 10px;
      }
    }

    .btn-lang {
      background-color: $c-white;
      margin-left: 5px;
      padding: 3px 5px;
      color: $c-black;
      font-weight: 600;
      font-size: 1.2rem;
      border: 1px solid $c-border;
      border-radius: 0.25rem;
    }
  }

  .main-navbar__menu-item {
    text-transform: uppercase;
    font-weight: 600;
    padding: 0 15px;
    font-size: 0.9rem;
    display: inline-block;
    position: relative;
    cursor: pointer;

    &::after {
      content: '';
      display: block;
      width: 0;
      height: 2px;
      margin-top: -2px;
      background-color: $c-dark;
    }

    &.router-link-active::after {
      animation: linear selected 0.3s;
      width: 70%;
    }
  }

  .main-navbar__sub-items {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $c-light;
    width: 150px;
  }

  .menu-item:hover .sub-item {
    display: block;
    color: #f11e1c;
  }

  .container-fluid {
    max-width: $container-max-width;
  }

  @keyframes selected {
    0% {
      width: 0;
    }
    100% {
      width: 70%;
    }
  }
</style>



