<template>
  <div class="route-settings">
    <b-form @submit="onSubmit">
      <div class="row">
        <div class="col-12">
          <h5>Настройки маршрута</h5>
        </div>
        <b-form-group label-for="albumName" label="Начальная точка:" class="col-12">
          <b-form-input id="startingPoint"
                        name="startingPoint"
                        type="text" v-model="form.startingPoint"
                        placeholder="Введите имя начальной точки"
          ></b-form-input>
        </b-form-group>
        <b-form-group label-for="albumName" label="Конечная точка:" class="col-12">
          <b-form-input id="endingPoint"
                        name="endingPoint"
                        type="text" v-model="form.endingPoint"
                        placeholder="Введите имя конечной точки"
          ></b-form-input>
        </b-form-group>
        <b-form-group label-for="albumName" label="Продолжительность:" class="col-12">
          <b-form-select  id="duration"
                          name="duration"
                          v-model="form.duration"
                          :options="hoursOptions">
          </b-form-select>
        </b-form-group>
        <b-form-group label-for="albumName" label="Вид транспорта:" class="col-12">
          <b-form-select  id="transportationType"
                          name="transportationType"
                          v-model="form.transportationType"
                          :options="transportationOptions" class="mb-3">
          </b-form-select>
        </b-form-group>
      </div>
      <div class="row">
        <!--<b-form-group label-for="selectedPoints" label="Точки маршрута" class="col-12">-->
          <!--<b-form-checkbox-group stacked v-model="form.selectedPoints" name="selectedPoints" :options="availablePoints">-->
          <!--</b-form-checkbox-group>-->
        <!--</b-form-group>-->
        <div v-for="point in availablePoints" class="col-12">
          <input type="checkbox" :id="point.value" :value="point.value" v-model="selectedPoints">
          <label :for="point.value">{{point.text}}</label>
        </div>
      </div>
      <div class="row">
        <b-form-group label-for="pointsCount" label="Количество точек:" class="col-12">
          <b-form-input id="pointsCount"
                        name="pointsCount"
                        type="range" v-model="form.pointsCount"
          ></b-form-input>
        </b-form-group>
        <b-form-group label-for="maxDeviation" label="Максимальное отклонение от основного пути:" class="col-12">
          <b-form-input id="maxDeviation"
                        name="maxDeviation"
                        type="text" v-model="form.maxDeviation"
                        placeholder="Введите vаксимальное отклонение км"
          ></b-form-input>
        </b-form-group>
        <b-form-group label-for="roadTime" label="Время в дороге:" class="col-12">
          <b-form-input id="roadTime"
                        name="roadTime"
                        type="text" v-model="form.roadTime"
                        placeholder="Введите время в пути часов"
          ></b-form-input>
        </b-form-group>
      </div>
      <div class="row">
        <div class="col">
          <button type="submit" class="btn btn-primary btn-block">Применить</button>
        </div>
      </div>
    </b-form>
  </div>
</template>

<script>

  export default {
    computed: {},
    data() {
      return {
        form: {
          startingPoint: '',
          endingPoint: '',
          maxDeviation: '',
          roadTime: '',
          duration: 8,
          transportationType: null,
          selectedPoints: [],
          pointsCount: null
        },
        transportationOptions: [
          { value: null, text: 'Выберите тип транспорта' },
          { value: 'car', text: 'Автомобиль' },
          { value: 'atbs', text: 'Автобус' },
          { value: 'skate', text: 'Самокат' }
        ],
        hoursOptions: [
          { value: 4, text: '4 часа' },
          { value: 6, text: '6 часов' },
          { value: 8, text: '8 часов' },
          { value: 12, text: '12 часов' }
        ],
        availablePoints: [
          { value: '4', text: 'Минск' },
          { value: '6', text: 'Замок Гедимина' },
          { value: '8', text: 'Лида' },
          { value: '12', text: 'Костел' }
        ]
      };
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault();
        console.log('Searched');
      }
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors.scss";

  .route-settings {
    background-color: $c-humming-bird;
    border-radius: 6px;
    padding: 10px;
    margin-top: 10px;
  }

</style>



