import * as types from './mutationTypes';

export const mutations = {
  [types.RECEIVE_PLACE_ALBUM](state, album) {
    state.placeAlbum = album;
  },
  [types.RECEIVE_BREADCRUMBS](state, breadcrumbs) {
    state.breadcrumbs = breadcrumbs;
  },
  [types.RECEIVE_PLACE_DESCRIPTION](state, description) {
    state.description = { name: description.name, description: description.description, point: {
      name: description.name,
      position: {
        lat: description.point.latitude,
        lng: description.point.longitude
      }
    } };
  },
  [types.RECEIVE_PLACE_OPPORTUNITIES](state, opportunities) {
    state.opportunities = opportunities;
  },
  [types.RECEIVE_PLACE_CONTACTS](state, contacts) {
    state.contacts = contacts;
  },
  [types.RECEIVE_PLACE_WHAT_TO_TAKE](state, whatToTake) {
    state.whatToTake = whatToTake;
  },
  [types.RECEIVE_PLACE_HOW_TO_GET_THERE](state, howToGetThere) {
    state.howToGetThere = howToGetThere;
  },
  [types.RECEIVE_PLACE_WORKING_HOURS](state, workingHours) {
    state.workingHours = workingHours;
  },
  [types.RECEIVE_PLACE_OTHER_INFO](state, otherInfo) {
    state.otherInfo = otherInfo;
  },
  [types.RECEIVE_LINKED_PLACES](state, linkedPlacesRes) {
    state.linkedPlaces = linkedPlacesRes.places;
  }
};



