import * as types from './mutationTypes';
import { getPlaceTypeahead } from '../../../api/search';

export const actions = {
  getPlaceTypeahead({ commit }, { searchText }) {
    getPlaceTypeahead(searchText).then(function (res) {
      commit(types.RECEIVE_PLACE_TYPEAHEAD, res);
    });
  }
};



