<template>
  <div class="vak-how-to-get-there" v-if="data">
  	<div class="mb-2">
  		<i class="fa fa-bus fa-1x align-middle" aria-hidden="true"></i>
  		<h5 class="info-title">Как добраться</h5>
  	</div>
  	<div class="section" v-show="!!data.address">
      <div>{{data.address.localityAddress}}</div>
      <div>{{data.address.placeAddress}}</div>
      <div v-html="data.text"></div>
    </div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {};
    },
    props: {
      data: Object
    },
    computed: {}
  };
</script>

<style>
.vak-how-to-get-there {
  padding-bottom: 10px;
}
</style>



