    <ul class="services pl-0">
      <li class="service" v-for="service in data">
        <a class="service-link" target="_blank" :href="service.linkUrl">{{service.title}} <i v-if="service.linkUrl" class="fa fa-external-link-square"></i></a>
      </li>
    </ul>
  </div>
</template>

<script>

  export default {
    props: {
      data: Array
    },
    computed: {}
  };

</script>

<style lang="scss">
  @import "../styles/colors";

  .opportunities {
    padding-bottom: 10px;
    margin-bottom: 10px;

    ul {
      list-style-type: none;
    }

    .service {
      padding-bottom: 5px;
    }

    .service-link {
      color: $c-dark;

      .fa {
        font-size: 0.8rem;
      }
    }
  }

</style>



