<template>
  <div class="searchbox">
    <div class="row">
      <b-form @submit="onSubmit" class="col-12">
        <div class="row">
          <autocomplete :at-label="'Начальная точка'" :at-placeholder="'Введите имя начальной точки'" v-model="form.startingPoint"></autocomplete>
          <autocomplete :at-label="'Конечная точка'" :at-placeholder="'Введите имя конечной точки'" v-model="form.endingPoint"></autocomplete>
          <b-form-group label-for="albumName" label="Продолжительность:" class="col-12 col-sm-6 col-lg-3">
            <b-form-select  id="duration"
                            name="duration"
                            v-model="form.duration"
                            :options="hoursOptions" class="mb-3">
            </b-form-select>
          </b-form-group>
          <b-form-group label-for="albumName" label="Вид транспорта:" class="col-12 col-sm-6 col-lg-3">
            <b-form-select  id="transportationType"
                            name="transportationType"
                            v-model="form.transportationType"
                            :options="transportationOptions" class="mb-3">
            </b-form-select>
          </b-form-group>
        </div>
        <div class="row">
          <div class="col"><a href="">Расширенный поиск</a></div>
          <div class="col">
            <button type="submit" class="btn btn-primary pull-right" :disabled="!(form.startingPoint && form.endingPoint)">Смотреть варианты</button>
          </div>
        </div>
      </b-form>
    </div>
    <div class="row">

    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'Vuex';
  import VakAutocomplete from '@/components/VakAutocomplete';

  export default {
    data() {
      return {
        form: {
          startingPoint: '',
          endingPoint: '',
          duration: 8,
          transportationType: 'car'
        },
        transportationOptions: [
//          { value: null, text: 'Выберите тип транспорта' },
          { value: 'car', text: 'Автомобиль' },
          { value: 'atbs', text: 'Автобус' },
          { value: 'skate', text: 'Самокат' }
        ],
        hoursOptions: [
          { value: 4, text: '4 часа' },
          { value: 6, text: '6 часов' },
          { value: 8, text: '8 часов' },
          { value: 12, text: '12 часов' }
        ]
      };
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault();
        console.log(this.form);
        this.$router.push({
          name: 'searchResults',
          query: {
            startingPoint: this.form.startingPoint.id,
            endingPoint: this.form.endingPoint.id,
            transportationType: this.form.transportationType,
            duration: this.form.duration
          }
        });
      },
      onChange() {
        if (this.form.startingPoint.length < 3) {
          return;
        }
        this.$store.dispatch('getPlaceTypeahead', { searchText: this.form.startingPoint });
      }
    },
    computed: {
      ...mapGetters([
        'placeTypeahead'
      ])
    },
    components: {
      'autocomplete': VakAutocomplete
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors.scss";

  .searchbox {
    background-color: $c-humming-bird;
    border-radius: 6px;
    padding: 10px;
    margin-top: 10px;
  }

</style>



