<template>
  <b-carousel id="carousel1"
              class="carousel"
              controls
              indicators
              background="#ababab"
              :interval="4000"
              img-width="1024"
              img-height="480"
  >

    <b-carousel-slide v-for="slide in slides">
      <img slot="img" class="d-block img-fluid w-100"
           v-bind:src="slide.imageUrl" alt="image slot">
      <h3>{{slide.title}}</h3>
      <p>{{slide.description}}</p>
    </b-carousel-slide>
  </b-carousel>
</template>

<script>

  export default {
    name: 'carousel',
    props: {
      slides: Array
    }
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import "../styles/colors.scss";

  .carousel {

    .carousel-caption {
      left: 0;
      right: 0;
      bottom: 0;
      text-align: left;
      padding: 10px;
      background-color: $c-white;
      opacity: 0;
      color: $c-primary;
      transition: opacity 0.3s;

      h3 {
        font-size: 0.9rem;
        margin: 0;
        font-weight: bold;
      }

      p {
        font-size: 0.7rem;
        margin: 0;
      }
    }

    .carousel-indicators {
      li {
        background-color: $c-dark;

        &.active {
          background-color: $c-light;
        }
      }
    }

    .carousel-control-prev, .carousel-control-next {
      width: 5%;
    }

    &:hover {
      .carousel-caption {
        opacity: 0.8;
      }
    }
  }

</style>



