<template>
  <div class="st-breadcrumbs-container">
    <ul class="st-breadcrumbs" v-if="data">
      <li class="st-breadcrumbs-item st-breadcrumbs-item__home">
        <a class="breadcrumb-link"><i class="fa fa-home"></i></a>
        <span class="splitter-end"></span>
      </li>
      <li class="st-breadcrumbs-item" v-for="breadcrumb in breadcrumbs">
        <span class="splitter-start"></span>
        <a class="breadcrumb-link">{{breadcrumb.title}}</a>
        <span class="splitter-end"></span>
      </li>
    </ul>
  </div>
</template>

<script>

  export default {
    props: {
      data: Array
    },
    computed: {
      breadcrumbs: function () {
        return this.data; //.slice(0,-1);
      }
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors";

  $breadcrumb_height: 13px;
  $breadcrumb_width: 8px;
  $splitter_pos_left: -13px;
  $splitter_pos_right: -14px;
  $item_margin: 13px;

  .st-breadcrumbs {
    list-style-type: none;
    padding: 10px 0;
    margin-bottom: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .st-breadcrumbs-item {
    padding: 1px 10px 1px 5px;
    color: $c-white;
    background-color: $c-downy;
    border-radius: 0.3rem;
    text-align: center;
    cursor: pointer;
    position: relative;
    margin-left: $item_margin;
    float: left;

    &:last-child {
      color: $c-dark;
    }

    &.st-breadcrumbs-item__home {
      width: 40px;
      margin: 0;
      background-color: $c-primary;

      .splitter-end {
        border-left: $breadcrumb_width solid $c-primary;
      }
    }
  }

  .st-breadcrumbs-item:last-child .fa {
    display: none;
  }

  .splitter-start {
    display: block;
    width: 0;
    height: 0;
    border-top: $breadcrumb_height solid $c-downy;
    border-left: $breadcrumb_width solid transparent;
    border-bottom: $breadcrumb_height solid $c-downy;
    border-right: $breadcrumb_width solid $c-downy;
    position: absolute;
    left: $splitter_pos_left;
    top: 0;
  }

  .splitter-end {
    display: block;
    width: 0;
    height: 0;
    border-top: $breadcrumb_height solid transparent;
    border-right: $breadcrumb_width solid transparent;
    border-bottom: $breadcrumb_height solid transparent;
    border-left: $breadcrumb_width solid $c-downy;
    position: absolute;
    right: $splitter_pos_right;
    top: 0;
  }

  .breadcrumb-main-section {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: -5px;
    line-height: 16px;
    display: inline-block;
  }
</style>



