export const getters = {
  placeAlbum (state) {
    return state.placeAlbum;
  },
  placeBreadcrumbs (state) {
    return state.breadcrumbs;
  },
  placeDescription (state) {
    return state.description;
  },
  placeContacts (state) {
    return state.contacts;
  },
  placeHowToGetThere (state) {
    return state.howToGetThere;
  },
  placeOpportunities (state) {
    return state.opportunities;
  },
  placeOtherInfo (state) {
    return state.otherInfo;
  },
  placeWhatToTake (state) {
    return state.whatToTake;
  },
  placeWorkingHours (state) {
    return state.workingHours;
  },
  linkedPlaces (state) {
    return state.linkedPlaces;
  }
};



