import Vue from 'vue';

function getDescription(placeId) {
  return Vue.http.get(`place/${placeId}/description`)
    .then(res => res.body);
}
function getAlbum(placeId) {
  return Vue.http.get(`place/${placeId}/album`)
    .then(res => res.body);
}
function getBreadcrumbs(placeId) {
  return Vue.http.get(`place/${placeId}/breadcrumbs`)
    .then(res => res.body);
}
function getWhatToTake(placeId) {
  return Vue.http.get(`place/${placeId}/what_to_take`)
    .then(res => res.body);
}
function getOpportunities(placeId) {
  return Vue.http.get(`place/${placeId}/services`)
    .then(res => res.body);
}
function getContacts(placeId) {
  return Vue.http.get(`place/${placeId}/contact`)
    .then(res => res.body);
}
function getWorkingHours(placeId) {
  return Vue.http.get(`place/${placeId}/working_hours`)
    .then(res => res.body);
}
function getHowToGetThere(placeId) {
  return Vue.http.get(`place/${placeId}/how_to_get_there`)
    .then(res => res.body);

}
function getOtherInfo(placeId) {
  return Vue.http.get(`place/${placeId}/other`)
    .then(res => res.body);
}
function getLinkedPlaces(placeId) {
  return Vue.http.get(`place/${placeId}/linked_places`)
    .then(res => res.body);
}

export {
  getDescription,
  getAlbum,
  getBreadcrumbs,
  getWhatToTake,
  getOpportunities,
  getContacts,
  getWorkingHours,
  getHowToGetThere,
  getOtherInfo,
  getLinkedPlaces
};



