<template>
  <div class="st-contacts">
    <div class="mb-2">
      <i class="fa fa-phone fa-1x align-middle" aria-hidden="true"></i>
      <h5 class="info-title">Контактная информация</h5>
    </div>
    <div class="section">
      <div class="row">
        <div class="col">
          <h6 class="section-title">Телефон:</h6>
        </div>
      </div>
      <div class="row">
        <ul class="st-contacts-listing">
          <li v-for="phone in data.phones">
            {{phone.type}}: {{phone.number}}
          </li>
        </ul>
      </div>
    </div>
    <div class="section">
      <div class="row">
        <div class="col">
          <h6 class="section-title">Электронная почта:</h6>
        </div>
      </div>
      <div class="row">
        <ul class="st-contacts-listing">
          <li v-for="mail in data.emails"><a :href="'mailto:' + mail.address">{{mail.address}}</a> ({{mail.description}})</li>
        </ul>
      </div>
    </div>
    <div class="section">
      <div class="row">
        <div class="col">
          <h6 class="section-title">Вебсайт:</h6>
        </div>
      </div>
      <div class="row">
        <ul class="st-contacts-listing">
          <li v-for="site in data.sites">
            <a :href="site.link">{{site.link}}</a> ({{site.description}})
          </li>
        </ul>
      </div>
    </div>
    <div class="section" v-show="data.freeText"><span v-html="data.freeText"></span></div>
  </div>

</template>

<script>
  export default {
    props: {
      data: Object
    },
    computed: {}
  };
</script>

<style>
  .st-contacts {
    padding-bottom: 10px;
  }

  .section-title {
    padding-top: 5px;
    margin-bottom: 0;
  }

  .st-contacts-listing {
    margin-bottom: 0;
  }

</style>



