<template>
  <div class="vak-card-carousel row">
    <div class="col-12 vak-card-carousel__container">
      <div class="row vak-card-carousel__page">
        <div :class="cardClass" v-for="card in current">
          <div class="card vak-card">
            <img class="card-img-top" :src="card.imageUrl"
                 alt="Card image cap">
            <div class="card-body" v-if="mode === modes.article">
              <h5 class="card-title">{{card.title}}</h5>
              <p class="card-text">{{card.description}}</p>
              <div class="vak-card-footer">
                {{card.date}}
                <a href="" class="link-details">Подробнее <i class="fa fa-caret-right"></i></a>
              </div>
            </div>
            <div class="card-body" v-else-if="mode === modes.place">
              <h5 class="card-title">{{card.title}}</h5>
              <p class="card-text">{{card.description}}</p>
            </div>
            <div class="card-body" v-else>
              <div class="card-duration">
                <i class="fa fa-clock-o"></i> 20 часов
                <i class="fa fa-road"></i> 800 км
              </div>
              <p class="card-text">{{card.description}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="vak-card-carousel__arrow-left"><i class="fa fa-chevron-circle-left" @click="prev()" v-if="currentPage"></i></div>
      <div class="vak-card-carousel__arrow-right"><i class="fa fa-chevron-circle-right" @click="next()" v-if="currentPage !== totalPages - 1"></i></div>
      <div class="vak-card-carousel__bullets">
        <i class="fa fa-circle" v-for="page in pages" :class="page.id === currentPage ? 'current' : ''" @click="selectPage(page.id)"></i>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "vak-card-carousel",
    data: function () {
      return {
        currentPage: 0,
        modes: {
          article: 'article',
          place: 'place',
          route: 'route'
        }
      };
    },
    props: {
      items: Array,
      mode: {
        type: String,
        default: 'article'
      },
      pageSize: {
        type: Number,
        default: 1
      }
    },
    methods: {
      prev() {
        if(!this.currentPage) {
          this.currentPage = this.totalPages - 1;
        } else {
          this.currentPage --;
        }
      },
      next() {
        if(this.currentPage === this.totalPages - 1) {
          this.currentPage = 0;
        } else {
          this.currentPage ++;
        }
      },
      selectPage(id) {
        this.currentPage = id;
      }
    },
    computed: {
      current() {
        return this.items.slice(this.currentPage * this.pageSize, (this.currentPage + 1) * this.pageSize);
      },
      cardClass() {
        let columnsPerPage = 12/this.pageSize;
        return `col-${columnsPerPage}`;
      },
      totalPages() {
        return Math.ceil(this.items.length/this.pageSize);
      },
      pages() {
        let pages = [];

        for (let i=0; i < this.totalPages; i++) {
          pages.push({
            id: i,
            items: this.items.slice(i * this.pageSize, (i + 1) * this.pageSize)
          });
        }
        return pages;
      }
    }
  };
</script>

<style scoped lang="scss">
  @import "../styles/colors";

  .vak-card-carousel {
  }

  .vak-card-carousel__container {
    position: relative;
  }

  .vak-card-carousel__arrow-left, .vak-card-carousel__arrow-right {
    position: absolute;
    display: flex;
    color: $c-white;
    top: 25%;

    .fa {
      cursor: pointer;
      font-size: 2rem;
      margin: 5px;
    }
  }

  .vak-card-carousel__arrow-left {
    left: 15px;
  }

  .vak-card-carousel__arrow-right {
    right: 15px;
  }

  .fa-caret-right {
    font-size: 0.8rem;
  }

  .link-details {
    text-decoration: none;
    float: right;
    color: $c-gray-light;
  }

  .vak-card-carousel__bullets {
    font-size: 0.6rem;
    color: $c-gray-light;
    padding: 10px;
    text-align: center;

    .fa-circle {
      margin: 2px;
      cursor: pointer;

      &.current {
        color: $c-gray;
      }
    }
  }
</style>



