import Vue from 'vue';
import Vuex from 'vuex';
import placeDetailsStore from './modules/placeDetailsStore';
import searchStore from './modules/searchStore';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    placeDetailsStore,
    searchStore
  },
  strict: true,
  plugins: []
});



