<template>
  <div role="group" class="col-12 col-sm-6 col-lg-3 b-form-group form-group">
    <label :for="atId" class="col-form-label pt-0">{{atLabel}}:</label>
    <div class="autocomplete-container" v-bind:class="{'has-value': query.length}">
      <input :id="atId" :name="atId" :placeholder="atPlaceholder" class="form-control" v-model="query" @keyup="onType">
      <i class="fa fa-times icon-clear" @click="clear()"></i>
      <ul class="results" v-if="items.length">
        <li v-for="item in items" :key="item.id" @click="select(item)">
          <span>{{ item.name }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

  import * as searchService from '../api/search';
  import { typeAheadModes } from '../enums/typeaheadModes';

  export default {
    name: 'vak-autocomplete',
    props: {
      atLabel: String,
      atPlaceholder: String,
      atId: {
        type: String,
        default: this._uid
      },
      atStartFrom: {
        type: Number,
        default: 3
      },
      atMode: {
        type: String,
        default: 'place'
      }
    },
    data() {
      return {
        query: '',
        items: []
      };
    },
    methods: {
      onType: function () {
        if (this.query.length >= this.atStartFrom) {
          return searchService[typeAheadModes[this.atMode]](this.query).then((res) => {
            this.items = res;
          });
        } else {
          this.items = [];
        }
        if (!this.query.length) {
          this.$emit('input', null);
        }
      },
      select: function (item) {
        this.query = item.name;
        this.items = [];
        this.$emit('input', item);
      },
      clear: function () {
        this.query = '';
      }
    },
    computed: {
    }
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../styles/variables";
  @import "../styles/colors";

  .autocomplete-container {
    position: relative;

    &.has-value {
      .icon-clear {
        visibility: visible;
        opacity: 1;
        transition: visibility 0s linear 0s, opacity 300ms;
      }

      .form-control {
        padding-right: 20px;
      }
    }
  }

  .results {
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: left;
    position: absolute;
    background-color: $c-white;
    border: 1px solid $c-gray-light;
    z-index: 1;
  }

  .results li {
    margin: 0;
    padding: 0.5em;
    list-style: none;
    width: 100%;
    transition: ease-in-out 0.5s;
    cursor: pointer;

    &:not(:last-child) {
      border-bottom: 1px solid $c-gray-light;
    }
  }

  .icon-clear {
    font-size: 1.2rem;
    position: absolute;
    right: 5px;
    top: 10px;
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 300ms, opacity 300ms;
  }
</style>



